.no-webp .img__contacto{
    background-image: url(../build/img/encuentra.jpg);
}

.webp .img__contacto{
    background-image: url(../img/encuentra.webp);
}

.img__contacto{
    background-position: center center;
    background-size: cover;
    padding: calc($separacion * 2)  0;
    display: flex;
    flex-direction: column;
    align-items: center;
    h2{
        font-size: 4rem;
        font-weight: $black;
        color: $blanco;
    }
    p{
        font-size: 1.8rem;
        color: $blanco;
    }
}
