  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear_derecha {
  display: flex;
  justify-content: flex-end;
}

.center {
  text-align: center;
}

.w-100 {
  width: 100%;
}

/* botones*/
.anuncio__btn {
  border: none;
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
}
.anuncio__btn:hover {
  background-color: #c77808;
  cursor: pointer;
}

.anuncio__btn--block {
  border: none;
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
}
.anuncio__btn--block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.anuncio__btn--verde {
  border: none;
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
}
.anuncio__btn--verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.btn__block--rojo {
  border: none;
  background-color: rgb(160, 29, 29);
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  width: 100%;
}
.btn__block--rojo:hover {
  background-color: #8a1919;
  cursor: pointer;
}

.btn__block--amarillo {
  border: none;
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
}
.btn__block--amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

/* darkmode */
.darkmode-action {
  background-color: #262626;
  color: #FFFFFF;
}
.darkmode-action p, .darkmode-action form, .darkmode-action .form p {
  color: #FFFFFF;
}
.darkmode-action h1, .darkmode-action h2, .darkmode-action h3, .darkmode-action h4, .darkmode-action legend {
  color: #71B100;
}
.darkmode-action .anuncio {
  background-color: #333333;
}
.darkmode-action .anuncio .precio {
  color: #E08709;
}
.darkmode-action .form legend {
  color: #71B100;
}
.darkmode-action .form fieldset textarea,
.darkmode-action .form fieldset input,
.darkmode-action .form fieldset select {
  background-color: #333333;
  color: #e1e1e1;
  border: 1px #4d4d4d solid;
}
.darkmode-action .form .alerta {
  color: #FFFFFF;
}

/**UI**/
.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin: 0;
}

.contenedor_footer {
  padding-top: 2.5rem;
}
.contenedor_footer .navegacion {
  visibility: visible;
  opacity: 1;
  padding: 0;
  height: auto;
}
.contenedor_footer .navegacion a:hover {
  background-color: transparent;
  color: #E08709;
  padding: 1rem;
}

.copy {
  color: #FFFFFF;
  text-align: center;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 80vh;
  }
}
.header.inicio .contenido_header {
  height: 80vh;
}

.contenido_header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido_header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2rem;
  line-height: 2;
  max-width: 30rem;
}
@media (min-width: 768px) {
  .contenido_header h1 {
    font-size: 3rem;
    max-width: 60rem;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .darkmode {
  width: 4rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
  padding: 1rem;
  border-radius: 50%;
  background-color: #71B100;
  cursor: pointer;
  filter: invert(100%);
}
@media (min-width: 768px) {
  .derecha .darkmode {
    margin: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.barra__flex {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
}
.barra__flex .barra__img {
  width: 80%;
}
@media (min-width: 768px) {
  .barra__flex .barra__img {
    max-width: 45rem;
  }
}

.mobile__img {
  width: 10%;
}

@media (min-width: 768px) {
  .mobile__img {
    display: none;
  }
}
.navegacion {
  height: 0;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in, opacity 0.3s linear, background 0.3s ease-in, height 10s linear;
  width: 100%;
}
@media (min-width: 768px) {
  .navegacion {
    visibility: visible;
    opacity: 1;
    display: flex;
    align-items: center;
    height: auto;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-align: center;
  padding: 1rem;
  transition: background 0.3ms ease-in-out;
}
.navegacion a:hover {
  background-color: #E08709;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
    padding: 0;
    width: 8rem;
  }
  .navegacion a:hover {
    background-color: transparent;
    color: #E08709;
  }
}

.hamburgesa {
  width: 10%;
}

.mostrar {
  height: auto;
  visibility: visible;
  opacity: 1;
  background-color: rgba(51, 51, 51, 0.8);
}

@media (min-width: 768px) {
  .mostrar {
    background-color: transparent;
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .iconos__nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 768px) {
  .contenedor__anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.contenedor__anuncios, .todas {
  margin: 3rem 0;
}

.anuncio {
  background-color: #eeeeee;
  border: 1px solid #d4d4d4;
  transition: all 0.3s ease;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .anuncio .anuncio {
    margin-top: 0;
  }
}
.anuncio:hover {
  transform: scale(1.05);
  border: 1px solid #aeaeae;
}
.anuncio .anuncio__contenido {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}
.anuncio h3 {
  line-height: 1;
  margin-bottom: 1rem;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 2.5rem;
}

.anuncio__iconos {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 50rem;
}
.anuncio__iconos li {
  flex: 1;
  display: flex;
  font-weight: 900;
}
.anuncio__iconos li p {
  font-size: 2rem;
  margin: 0;
}
.anuncio__iconos li img, .anuncio__iconos li picture {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
@media (min-width: 1024px) {
  .anuncio__iconos li p {
    font-size: 2.5rem;
  }
  .anuncio__iconos li img, .anuncio__iconos li picture {
    flex: 0 0 4rem;
    margin-right: 1.5rem;
  }
}

.anuncio_altura {
  height: 120px;
  overflow-y: scroll;
}

.no-webp .img__contacto {
  background-image: url(../build/img/encuentra.jpg);
}

.webp .img__contacto {
  background-image: url(../img/encuentra.webp);
}

.img__contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.img__contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.img__contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.blog__entrada {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .blog__entrada {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.blog__entrada:last-of-type {
  margin-bottom: 0;
}

.blog__texto a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .blog__texto a {
    margin-top: 0;
  }
}
.blog__texto h4 {
  margin: 0;
  line-height: 1.4;
}
.blog__texto h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.blog__texto span {
  color: #E08709;
  font-weight: 700;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.3rem;
  padding: 1rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 4rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -3rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

@media (min-width: 1024px) {
  .nosotros {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.nosotros .nosotros__img {
  display: flex;
  align-items: center;
}
.nosotros .nosotros__txt {
  text-align: justify;
}
.nosotros .nosotros__txt-bold {
  font-weight: 900;
}

.seccion__titulo {
  line-height: 1.2;
}

.form fieldset {
  border: 1px solid #E08709;
}
.form p {
  font-size: 2rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.form legend {
  font-size: 2.5rem;
  color: #333333;
  padding: 1rem;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit], [type=radio]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  margin-bottom: 2rem;
  border-radius: 1rem;
}
.form .contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
}
.form textarea {
  height: 20rem;
}

/* Paginas internas */
.alerta {
  padding: 1rem;
  color: black;
  text-align: center;
  font-weight: bold;
  margin: 1rem 0;
  text-transform: uppercase;
}
.alerta.error {
  background-color: rgb(160, 29, 29);
}
.alerta.exito {
  background-color: #71B100;
}

.tabla {
  overflow-x: scroll;
}

@media (min-width: 768px) {
  .tabla {
    overflow-x: auto;
  }
}
table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades td {
  padding: 2rem;
}
table.propiedades img {
  width: 100%;
  height: 10rem;
  object-fit: contain;
}
table.propiedades .propiedades__num {
  text-align: center;
  font-weight: 700;
  font-size: 2rem;
}

.small {
  width: 100%;
  height: 20rem;
  object-fit: contain;
}