.testimonial{
    background-color: $verde;
    font-size: 2.3rem;
    padding: 1rem;
    color: $blanco;
    border-radius: 2rem;

    blockquote{
        padding-left: 4rem;
        position: relative;
        &::before{
            content: '';
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            width: 6rem;
            height: 8rem;
            display: block;
            position: absolute;
            left:-3rem;
        }
    }
    p{
        text-align: right;
        color: $blanco;
    }
}